html,
body {
  max-height: fit-content;
  overflow-x: hidden;
  max-width: 100%;
  background-color: #7258f8;
}

.title {
  line-height: 87px;
  font-weight: 400;
  font-size: 80px;
  margin-right: 80px;
  letter-spacing: -0.025;
  text-align: center;
  background: linear-gradient(90deg, #ffe23d 14.46%, #00ffd2 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  z-index: 1;
}
.span {
  font-weight: 700;
}
.code {
  position: absolute;
  top: -310px;
  display: list-item;
  left: 810px;
  background: linear-gradient(to top, #7258f8 25%, #ffffff44 45%, #7258f8 100%);
  background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 230px;
  letter-spacing: -20px;
  font-family: sans-serif;
  font-weight: 1000;
  z-index: 0;
}
.head {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.row-2 {
  padding: 40px;
  display: flex;
  align-items: center;
  justify-items: center;
  justify-content: space-evenly;
}
.input {
  margin-left: 40px;
  width: 850px;
  height: 80px;
  text-align: center;
  line-height: 70px;
  border: none;
  font-weight: bold;
  font-size: larger;
  color: white;
  background: #4c38af;
  border-radius: 35px;
}
.btn {
  width: 150px;
  height: 70px;
  cursor: pointer;
  border: none;
  font-size: larger;
  color: white;
  background-color: #2cbea4;
  border-radius: 30px;
}
.top-img {
  float: left;
  width: 90px;
  height: 110px;
  margin-right: 70px;
}
.g33-yellow {
  float: left;
  width: 100px;
  height: 100px;
}
.arrow {
  width: 180px;
  height: 160px;
}
.shape {
  padding: -20px;
  margin-top: 100px;
  width: 290px;
  height: 290px;
}
.code-board {
  padding: 16px 50px;
  max-width: 900px;
  height: 450px;
}
.last {
  margin-left: 110px;
  width: 200px;
  height: 200px;
}
.frame {
  position: absolute;
  width: 120px;
  height: 120px;
  left: 990px;
  top: 480px;
}
.frame-2 {
  position: absolute;
  left: 372px;
  top: 551px;
  width: 31px;
  height: 31px;
}
.final {
  display: flex;
  align-items: center;
}
.container {
  display: flex;
  margin-left: 50px;
  align-items: center;
}
